import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { CommentersComponent } from './commenters.component';

const routes: Routes = [
  { path: '', component: CommentersComponent }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CommentersRoutingModule { }



